@charset "UTF-8";
@font-face {
  font-family: "Akkurat LL";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/AkkuratLLCyr-Light.woff2") format("woff2"), url("../fonts/AkkuratLLCyr-Light.woff") format("woff");
}
@font-face {
  font-family: "Akkurat LL";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/AkkuratLLCyr-Regular.woff2") format("woff2"), url("../fonts/AkkuratLLCyr-Regular.woff") format("woff");
}
@font-face {
  font-family: "AkkuratLL";
  font-style: bold;
  font-weight: 700;
  font-display: swap;
  src: url("./fonts/AkkuratLL-Regular.woff2") format("woff2"), "./fonts/AkkuratLL-Regular.woff" format("woff");
}
*, *::before, *::after {
  box-sizing: inherit;
}

html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  padding: 0;
  font-family: "Akkurat LL", "Helvetica", "Arial", sans-serif;
  font-weight: normal;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.hidden {
  position: absolute;
  width: 0.1px !important;
  height: 0.1px !important;
  margin: 0 !important;
  padding: 0 !important;
  opacity: 0 !important;
  overflow: hidden !important;
  z-index: -1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

section > * > *, .footer > * > *, .projects__card > * > *, .projects__title > * > * {
  opacity: 0;
}

.main__wrapper.projects > * > * {
  opacity: 1;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  min-height: 50px;
  padding: 12px 36px;
  background: #E0804E;
  border-radius: 64px;
  border: 0;
  text-align: center;
  vertical-align: middle;
  font-family: "Akkurat LL", "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 35px;
    letter-spacing: -1px;
  }
}

.button:hover {
  background-color: #FAB38E;
}

.form__btn-submit {
  width: 175px;
  z-index: 5;
}

.about-opportunities__link {
  padding: 13px 55px;
  width: 210px;
}

.slick-slider {
  position: relative;
}

.slick-prev {
  position: absolute;
  content: "";
  width: 27%;
  height: 100%;
  font-size: 0;
  left: 0;
  top: 0;
  z-index: 5;
  background: linear-gradient(94.29deg, rgba(0, 49, 55, 0.6) 0.06%, rgba(243, 241, 216, 0) 87.25%);
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: url(../img/project-arrow-mob-left.svg), e-resize;
}
@media (min-width: 1024px) {
  .slick-prev {
    cursor: url(../img/slider-arrow-left-cursor.svg), e-resize;
  }
}

.slick-next {
  transform: rotate(180deg);
  position: absolute;
  content: "";
  width: 27%;
  height: 100%;
  font-size: 0;
  right: 0;
  top: 0;
  z-index: 5;
  background: linear-gradient(94.29deg, rgba(0, 49, 55, 0.6) 0.06%, rgba(243, 241, 216, 0) 87.25%);
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: url(../img/project-arrow-mob.svg), e-resize;
}
@media (min-width: 1024px) {
  .slick-next {
    cursor: url(../img/slider-arrow-right-cursor.svg), e-resize;
  }
}

.slick-dots {
  transform: translateX(-50%);
  position: absolute;
  bottom: 20px;
  left: 50%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex !important;
  justify-content: center;
  gap: 10px;
}

.slick-dots li button {
  display: block;
  width: 10px;
  height: 10px;
  font-size: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 10px 10px;
  background-image: url("../img/project-slider-point.svg");
  border: none;
}
@media (min-width: 768px) {
  .slick-dots li button {
    width: 12px;
    height: 12px;
    background-size: 12px 12px;
  }
}
@media (min-width: 1440px) {
  .slick-dots li button {
    width: 14px;
    height: 14px;
    background-size: 14px 14px;
  }
}

.slick-dots li.slick-active button {
  background-image: url("../img/project-slider-active-point.svg");
}
@media (min-width: 768px) {
  .slick-dots li.slick-active button {
    width: 12px;
    height: 12px;
    background-size: 12px 12px;
  }
}
@media (min-width: 1440px) {
  .slick-dots li.slick-active button {
    width: 14px;
    height: 14px;
    background-size: 14px 14px;
  }
}

