.card {
  padding-bottom: 46px;
}

/*
312 80 312
392 24 288    = 312
704 = 100px
45% 10% 45%
55% 45%
*/
@media (min-width: 768px) {
  .card:nth-child(4n+1) {
    /**/
    width: 55%;
    padding-right: 10%;
  }

  .card:nth-child(4n+2) {
    /* */
    width: 45%;
    padding-top: 5%;
  }

  .card:nth-child(4n) {
    width: 55%;
  }

  .card:nth-child(4n+3) {
    /*  */
    width: 45%;
    padding-left: 4%;
    order: 3;
    margin-top: -25%;
  }

  /*.card:nth-child(4n+3) {order:3; padding-left: 20%;}*/
}
